<template>
  <div class="part-six">
    <h3 class="text-center text-top">项目介绍</h3>
    <p class="text-article">担任青芒管理后台的前端负责人，这是一个学校学生社团集管理，招新，活动通知等操作的微信小程序的后台管理系统。首先理清整体架构，选择Vue + ElementUI作为技术栈，用axios与后端接口交互，并把路由设置好，主体框架包括侧边栏与标题栏在app.vue，各个路由对应的页面分别为各个vue文件，分工合作完成，这样实现了分工明确，可多人协同参与项目。</p>
  </div>
</template>

<script>
export default {
  name: "part-six",
  data() {
    return {
      
    };
  },
  
};
</script>

<style scoped>
@-webkit-keyframes masked-animation {
    0% {
            background-position: 0 0
        }
    to {
            background-position: -100% 0
        }
}
@keyframes masked-animation {
    0% {
            background-position: 0 0
        }
    to {
            background-position: -100% 0
        }
}

.part-six {
  width: 100%;
  height: 450px;
  border: 40px solid transparent;
  border-image: url("~@/./assets/img/border_image.png") 30 30 stretch;
  background: #18202d;
  text-align: left;
}

.text-center {
    text-align: center;
    margin-bottom: 30px;
    color: hsla(0, 0%, 100%, .65);
    font-weight: 800;
    background-image: -webkit-linear-gradient(left, #00bfff, #207ffc 25%, #00eeff 50%, #0066ff 75%, #00ffdd);
    -webkit-text-fill-color: transparent;
    -webkit-background-clip: text;
    background-clip: text;
    -webkit-background-size: 200% 100%;
    background-size: 200% 100%;
    -webkit-animation: masked-animation 2s infinite linear;
    animation: masked-animation 2s infinite linear;

}
.text-center:hover {
     -webkit-animation: masked-animation 0.5s infinite linear;
     animation: masked-animation 0.5s infinite linear;
     cursor: pointer;
}
.text-article {
    text-indent:2em;
    line-height: 30px;
    font-size: 0.9em;
    color: rgb(253, 239, 239);
}

</style>


